import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input } from 'antd';
import { dormitoryRooms } from 'dump/data';

const handleInitialValue = initialValue => {
  const initialValueDefault = {
    dormitoryBuildingCode: '',
    floorNumber: '',
    roomNumber: '',
    capacity: 100,
    device: '',
    price: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'FormDormitoryRoomPage' })
class FormDormitoryRoomPage extends Component {
  state = {
    dataId: false,
    initialValue: {},
  };

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ dataId: match.params.id });
      this.getDataById(match.params.id);
    }
  };

  getDataById = code => {
    const dataMap = dormitoryRooms.filter(data => data.id === code)[0];
    const initialValue = handleInitialValue(dataMap);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    return (
      <Card title={`${dataId ? 'Cập nhật' : 'Thêm'} phòng KTX`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Tòa KTX" {...formItemLayout}>
              {getFieldDecorator('dormitoryBuildingCode', {
                initialValue: initialValue.dormitoryBuildingCode,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Tầng" hasFeedback>
              {getFieldDecorator('floorNumber', {
                initialValue: initialValue.floorNumber,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số phòng" hasFeedback>
              {getFieldDecorator('roomNumber', {
                initialValue: initialValue.roomNumber,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Sức chứa" hasFeedback>
              {getFieldDecorator('capacity', {
                initialValue: initialValue.capacity,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Thiết bị" hasFeedback>
              {getFieldDecorator('device', {
                initialValue: initialValue.device,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Số tiền" hasFeedback>
              {getFieldDecorator('price', {
                initialValue: initialValue.price,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel onClick={() => this.props.history.goBack()} />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormDormitoryRoomPage {...props} />
  </Page>
));



